:root {
  --greenColor: #0da30d;
  --redColor: #ee0d0d;
  --deleteButtonColor:#ee5344;
  --addButtonColor:#75cdfc;
}

* {box-sizing: border-box;}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  display: inline-block;
  color: #2994c3;
  margin: 5px;
}

h2 {
  margin-left: 10px;
  text-decoration: underline;
}

p {
  display: inline-block;
  margin: 5px;
}

button {
  display: inline-block;
  padding: 2px 4px;
  border-radius: 5px;
  border: none;
  margin-left: 15px;
}

.deleteButton:hover {
  background-color: var(--deleteButtonColor);
}

.addButton:hover {
  background-color: var(--addButtonColor);
}

.filterContainer {
  margin-bottom: 20px;
}

.NumberList {
  width: fit-content;
  padding: 15px;
  margin-left: 20px;
  background-color: rgb(164, 193, 218);
  border-radius: 5px;
}

.success, .error {
  display: inline-block;
  background-color: var(--greenColor);
  color: #ffffff;
  font-size: 22px;
  font-weight: 500;
  border-radius: 8px;
  padding: 10px;
  margin-left: 10px;
}

.error {
  background-color: var(--redColor);
}